@import "utilityes";

.navbar {
  z-index: 10;
  padding: 0 30px;
  position: sticky;
  height: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  background-color: $light-color;
  .social {
    width: 82px;
    color: $dark-color;
    display: flex;
    justify-content: space-between;
  }
  .logo {
    width: 82px;
    img {
      width: 100%;
    }
  }
}

#header {
  .slider {
    color: $light-color;
    position: relative;
    width: 100vw;
    height: 70vh;
    display: flex;
    overflow: hidden;
    .slide--text {
      transition: all 1s;
    }
    .slide {
      z-index: 2;
      h1 {
        font-size: 4rem;
      }
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      text-align: center;
      align-items: center;
      justify-content: center;
      .bg1 {
        position: absolute;
        z-index: -2;
        width: 100%;
        height: 100%;
        background: url(./Img/bg1.jpg) no-repeat center/cover;
      }
      .bg2 {
        position: absolute;
        z-index: -2;
        width: 100%;
        height: 100%;
        background: url(./Img/tribune.jpg) no-repeat center/cover;
      }
    }
    .dots {
      z-index: 2;
      position: absolute;
      bottom: 5%;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
    }
    .dots__dot {
      border: none;
      background-color: #b9b9b9;
      opacity: 0.7;
      height: 1rem;
      width: 1rem;
      border-radius: 50%;
      margin-right: 1.75rem;
      cursor: pointer;
      transition: all 0.5s;

      /* Only necessary when overlying images */
      /* box-shadow: 0 0.6rem 1.5rem rgba(0, 0, 0, 0.7); */
    }

    .dots__dot:last-child {
      margin: 0;
    }

    .dots__dot--active {
      background-color: #fff;
      opacity: 1;
    }
  }
}
